import React, { Component } from 'react';
export default  class Resume extends Component {
  render() {
    let resumeData = this.props.resumeData;
    return (
      <section id="resume">

         <div className="row education">

            <div className="three columns header-col">
               <h1><span>Education</span></h1>
            </div>

            <div className="nine columns main-col">
              {
                resumeData.education && resumeData.education.map((item)=>{
                  return(
                    <div className="row item">
                       <div className="twelve columns">
                          <h3>{item.UniversityName}</h3>
                          <p className="info">
                          {item.specialization}
                          <span>&bull;</span> <em className="date">{item.MonthOfPassing} {item.YearOfPassing}</em></p>
                          <p>
                          {item.Achievements}
                          </p>
                       </div>
                    </div>
                  )
                })
              }
            </div>
         </div>
        <div className="row work">
            <div className="three columns header-col">
               <h1><span>Work</span></h1>
            </div>

            <div className="nine columns main-col">
              {
                resumeData.work && resumeData.work.map((item) => {
                  return(
                    <div className="row item">
                       <div className="twelve columns">
                          <h3>{item.CompanyName}</h3>
                          <p className="info">
                          {item.specialization}
                          <span>&bull;</span> <em className="date">{item.MonthOfLeaving} {item.YearOfLeaving}</em></p>
                          <p>
                          {item.Achievements}
                          </p>
                       </div>

                    </div>

                  )
                })
              }
            </div> 
         </div>

  <section className="section services" id="services">
  <div className="title-wrapper">
    <h2 className="title"><span className="subtitle">skills</span></h2>
  </div>
  <div className="section-center services-center">
    <article className="service">
      <span className="service-icon">
        <i className="devicon-html5-plain" style={{color: 'white'}} />
      </span>
      <div className="service-info">
        <h4 className="service-title">HTML5</h4>
      </div>
    </article>
    <article className="service">
      <span className="service-icon">
        <i className="devicon-css3-plain-wordmark" style={{color: 'white'}} />
      </span>
      <div className="service-info">
        <h4 className="service-title">css3</h4>
      </div>
    </article>
    <article className="service">
      <span className="service-icon">
        <i className="devicon-javascript-plain" style={{color: 'white'}} />
      </span>
      <div className="service-info">
        <h4 className="service-title">javascript</h4>
      </div>
    </article>
    <article className="service">
      <span className="service-icon">
        <i className="devicon-php-plain" style={{color: 'white'}} />
      </span>
      <div className="service-info">
        <h4 className="service-title">PHP</h4>
      </div>
    </article>
    <article className="service">
      <span className="service-icon">
        <i className="icon-cplusplus" style={{color: 'white'}} />
      </span>
      <div className="service-info">
        <h4 className="service-title">c++</h4>
      </div>
    </article>
    <article className="service">
      <span className="service-icon">
        <i className="devicon-java-plain" style={{color: 'white'}} />
      </span>
      <div className="service-info">
        <h4 className="service-title">java</h4>
      </div>
    </article>
    <article className="service">
      <span className="service-icon">
        <i className="devicon-python-plain" style={{color: 'white'}} />
      </span>
      <div className="service-info">
        <h4 className="service-title">python</h4>
      </div>
    </article>
    <article className="service">
      <span className="service-icon">
        <i className="devicon-mysql-plain" style={{color: 'white'}} />
      </span>
      <div className="service-info">
        <h4 className="service-title">mysql</h4>
      </div>
    </article>
    <article className="service">
      <span className="service-icon">
        <i className="devicon-swift-plain" style={{color: 'white'}} />
      </span>
      <div className="service-info">
        <h4 className="service-title">swift</h4>
      </div>
    </article>
  </div>



</section>

         
              
              



   				

   		

      </section>
    );
  }
}
