import React, { Component } from 'react';
export default class About extends Component {
  render() {
    let resumeData = this.props.resumeData;
    return (
      <section id="about">
         <div className="row">

            <div className="three columns">

               <img className="profile-pic"  src="images/portfolio/RAE.jpg" alt="" />

            </div>

            <div className="nine columns main-col">

               <h2>About Me</h2>
               <p>
               {
                 resumeData.aboutme
               }
               </p>

               <div className="row">

                  <div className="columns contact-details">

                  <h2>Contact Details</h2>
                  <p className="address">
       						<span>{resumeData.name}</span>
                     <br></br>
       						   <span>
                     {resumeData.address}
                    </span>
                    <br></br>
                    <span>{resumeData.Contactnumber}</span>
       					   </p>
                         <a href="https://drive.google.com/file/d/1I0crEIkgHavA5nRL26l3FNXSS0hyOPtW/view?usp=sharing" className="btn-primary">Download CV</a>

                  </div>
               </div>
            </div>
         </div>
      </section>
    );
  }
}
